import React, { Component } from "react";
import { Table, Checkbox, Dropdown, Input } from "semantic-ui-react";

const lengthTypeOptions = [
  { key: " fixed", text: "Fixed", value: "fixed" },
  { key: "variable", text: "variable", value: "variable" }
];

export default class TableRowWithSlider extends React.Component {
  constructor(props) {
    super(props);
    const { initialState } = this.props;
    this.state = {
      fieldCheckbox: initialState,
      lengthType: ""
    };
  }

  checkbox = () => {
    this.setState({ fieldCheckbox: !this.state.fieldCheckbox }, () => {
      console.log("checkbox --> ", this.state.fieldCheckbox);
      const { name, dataType, isArray, position, box } = this.props;
      box({
        name,
        dataType,
        isArray,
        position,
        isChecked: this.state.fieldCheckbox
      });
    });
  };

  render() {
    const {
      name,
      dataType,
      isArray,
      position,
      handleChange,
      lengthType,
      onLengthTypeChange
    } = this.props;
    const { fieldCheckbox } = this.state;
    return (
      <React.Fragment>
        <Table.Row>
          <Checkbox
            style={{ marginTop: "20px" }}
            checkbox
            onChange={this.checkbox}
          />
          <Table.Cell>{name}</Table.Cell>
          <Table.Cell>{dataType}</Table.Cell>
          <Table.Cell>{JSON.stringify(isArray)}</Table.Cell>
          <Table.Cell>{position}</Table.Cell>
          <Table.Cell>
            <Dropdown
              placeholder="Pick a length Type:"
              clearable
              selection
              search
              fluid
              noResultsMessage="Please search again"
              label="lengthType"
              multiple={false}
              options={lengthTypeOptions}
              header="Choose a Length Type"
              onChange={e => onLengthTypeChange(e, name)}
              value={lengthType}
              disabled={!this.state.fieldCheckbox}
            />
          </Table.Cell>
          <Table.Cell>
            <Input
              onChange={e => handleChange(e, name)}
              value={this.state.size}
              type="number"
              name="size"
              min="1"
              placeholder="1"
              disabled={!this.state.fieldCheckbox}
            />
          </Table.Cell>
          <Table.Cell>
            <Input
              onChange={e => handleChange(e, name)}
              value={this.state.maxArrayElements}
              type="number"
              name="maxArrayElements"
              placeholder="1"
              min="1"
              max="100"
              disabled={!this.state.fieldCheckbox}
            />
          </Table.Cell>
        </Table.Row>
      </React.Fragment>
    );
  }
}
